import React from "react";




class Myimage extends React.Component {
        
    render(){
        return(
            <span>
           
          
                     <img Style={ "max-width:50%;"} src={this.props.src} alt={this.props.src} className="shadow m-2 p-2" />
           
    
    
            </span>

        )  

    }

}
    


export default Myimage;